.FiltersForm header{display:flex;flex-direction:row;align-items:center;justify-content:space-between}.FiltersForm header .title{display:flex;flex-direction:row;align-items:center;justify-content:space-between}.FiltersForm header .title svg{margin-left:0.5rem}.FiltersForm section{padding:var(--spacing-sm) 0}.FiltersForm section .sectionLabel{display:flex;flex-direction:row;align-items:center;justify-content:space-between;margin-bottom:var(--spacing-sm)}.FiltersForm section.amount .sliderLabel{display:flex;flex-direction:row;align-items:center;justify-content:space-between}.FiltersForm section.amount .sliderContainer{padding:0 0.5rem}.FiltersForm section.category .categoryChips{display:flex;flex-wrap:wrap}.FiltersForm section.category .categoryChips>*{margin:0 0.2rem 0.3rem 0}.FiltersForm section.category .categoryActions{display:flex;flex-direction:row;align-items:center}

